.futuristic-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row !important;
  padding: 0 40px;
  height: 70px;

  @media screen and (max-width: 768px) {
    height: 44px;
    padding: 0 16px;
  }

  .logo {
    width: 128px;
    height: 36px;

    @media screen and (max-width: 768px) {
      width: 110px;
      height: 34px;
    }
  }

  .connect-button {
    background: linear-gradient(243.59deg, #71FF3A 14.24%, #00D3BA 83.41%);
    border-radius: 8px;
    height: 38px;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;

    @media screen and (max-width: 768px) {
      font-size: 14px;
      height: 32px;
    }
  }
}