.header{
    background: linear-gradient(243.59deg, #71FF3A 14.24%, #00D3BA 83.41%);
    border-radius: 16px;
    height: 120px;
    color: #000;
    padding: 24px;
    position: relative;

    @media screen and (max-width: 768px){
        margin: 0 16px;
        height: 92px;
        box-sizing: border-box;
        padding: 16px;
    }

    &::after{
        display: block;
        content: ' ';
        background: url(./img//bg.png);
        background-size: cover;
        width: 250px;
        height: 100%;
        position: absolute;
        top: 0;
        right: 0;
    }

    .tit{
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
        @media screen and (max-width: 768px){
            font-size: 14px;
        }
    }

    .num{
        font-family: 'Kanit';
        font-style: normal;
        font-weight: 500;
        font-size: 32px;
        line-height: 48px;
        @media screen and (max-width: 768px){
            font-size: 24px;
            line-height: 36px;
        }
    }
}