.footer{
  max-width: 1200px;
  margin: 0 auto;
  @media screen and (max-width: 768px){
    display: none;
  }

  .main{
    display: flex;
    justify-content: space-between;
    padding: 30px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    .logo{
      width: 128px;
      height: 36px;
      cursor: pointer;
    }
    .community{
      display: flex;
      align-items: center;
      a{
        margin: 0 0 0 32px;
        .icon{
          width: 20px;
          height: 20px;
          fill: rgba(255, 255, 255, 0.4);
          transition: all 0.3s;
          &:hover{
            fill: rgba(255, 255, 255, 1);
          }
        }
      }
    }
  }
  .copyright{
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    margin: 30px 0 40px;
    text-align: center;
    color: rgba(#fff, .3);
  }
}